import api from '@/api'

// initial state
const state = {
	money: {
		value: 0,
		unit: 'Руб.'
	}
}

// getters
const getters = {
}

// actions
const actions = {
	shopInitUesrMoney ({ commit, state }, payload) {
		return api.shop.initUesrMoney(payload.data, payload).then(function (data) {
			if (payload.callback && typeof payload.callback === 'function') {
				payload.callback(data)
			}
		})
	},
	shopGetProductItems ({ commit, state }, payload) {
		return api.shop.getProductItems(payload.data, payload).then(function (data) {
			if (payload.callback && typeof payload.callback === 'function') {
				payload.callback(data)
			}
		})
	},
	shopBuyProduct ({ commit, state }, payload) {
		return api.shop.buyProduct(payload.data, payload).then(function (data) {
			if (payload.callback && typeof payload.callback === 'function') {
				payload.callback(data)
			}
		})
	}
}

// mutations
const mutations = {
	moneyValue (state, value) {
		state.money.value = value
	},
	moneyUnit (state, value) {
		state.money.unit = value
	},
	changeMoneyValue (state, value) {
		state.money.value = value
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}



