import Vue from 'vue'
import Vuex from 'vuex'

import app from './modules/app'
import user from './modules/user'
import meter from './modules/meter'
import referral from './modules/referral'
import shop from './modules/shop'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		cometRooms: [],

		cometConnect: false,
		cometEventReload: null,
		cometEventUpdate: null,
		soket: {
			moneyCount: null
		}
	},
	mutations: {
		cometRooms: (state, rooms) => {
			state.cometRooms = rooms
		},
		cometRoomPush: (state, room) => {
			state.cometRooms.push(room)
		},
		cometRoomRemove: (state, room) => {
			var ind = state.cometRooms.indexOf(room)
			state.cometRooms.splice(ind, 1)
		},
		SOCKET_CONNECT: (state) => {
			state.cometConnect = true
		},
		SOCKET_RELOAD: (state, message) => {
			state.cometEventReload = message
		},
		SOCKET_UPDATE: (state, message) => {
			state.cometEventUpdate = message
		},
		SOCKET_MONEY_COUNT: (state, message) => {
			state.soket.moneyCount = message
		}
	},
	actions: {
		socket_connect: (context) => {
			var vue = new Vue()
			for (var i in context.state.cometRooms) {
				vue.$socket.emit('join', context.state.cometRooms[i])
			}
		},
		socket_room_join: (context, room) => {
			if (context.state.cometRooms.indexOf(room) === -1) {
				var vue = new Vue()
				vue.$socket.emit('join', room)
				context.commit('cometRoomPush', room)
			}
		},
		socket_room_leave: (context, room) => {
			if (context.state.cometRooms.indexOf(room) !== -1) {
				var vue = new Vue()
				vue.$socket.emit('leave', room)
				context.commit('cometRoomRemove', room)
			}
		}
	},
	modules: {
		app,
		user,
		meter,
		shop,
		referral
	}
})



