import api from './index'

export default {
	getProductItems (data, payload) {
		return api.fetch('/app/shop/get-product-items', {
			method: 'GET'
		}, payload)
	},
	buyProduct (data, payload) {
		return api.fetch('/app/shop/buy-product', {
			method: 'POST',
			body: api.buildFormData(api.csrfPrepare(data))
		}, payload)
	},
	initUesrMoney (data, payload) {
		return api.fetch('/app/shop/init-user-money', {
			method: 'GET'
		}, payload)
	}
}



