import api from '@/api'

// initial state
const state = {
}

// getters
const getters = {
}

// actions
const actions = {
	referralGetNewLink ({ commit, state }, payload) {
		return api.referral.getNewLink(payload.data, payload).then(function (data) {
			if (payload.callback && typeof payload.callback === 'function') {
				payload.callback(data)
			}
		})
	},
	referralGetLinkItems ({ commit, state }, payload) {
		return api.referral.getLinkItems(payload.data, payload).then(function (data) {
			if (payload.callback && typeof payload.callback === 'function') {
				payload.callback(data)
			}
		})
	}
}

// mutations
const mutations = {
}

export default {
	state,
	getters,
	actions,
	mutations
}



