
const DashboardLayout = () => import(/* webpackChunkName: "start" */'../components/Dashboard/Layout/DashboardLayout.vue')

const NotFound = () => import(/* webpackChunkName: "start" */'../components/GeneralViews/NotFoundPage.vue')

const Login = () => import(/* webpackChunkName: "start" */'src/components/Dashboard/Views/Pages/Login.vue')
const Forgot = () => import(/* webpackChunkName: "start" */'src/components/Dashboard/Views/Pages/Forgot.vue')
const Register = () => import(/* webpackChunkName: "start" */'src/components/Dashboard/Views/Pages/Register.vue')
const UserActivate = () => import(/* webpackChunkName: "start" */'src/components/Dashboard/Views/Pages/UserActivate.vue')
const ResetPassword = () => import(/* webpackChunkName: "start" */'src/components/Dashboard/Views/Pages/ResetPassword.vue')

// const Index = () => import(/* webpackChunkName: "index" */'src/components/Dashboard/Views/Index/Index.vue')
const ReferralIndex = () => import(/* webpackChunkName: "referral" */'src/components/Modules/Referral/Index.vue')

const ShopIndex = () => import(/* webpackChunkName: "shop" */'src/components/Modules/Shop/Index.vue')

let loginPage = {
	path: '/login',
	name: 'Войти',
	component: Login
}

let forgotPage = {
	path: '/forgot',
	name: 'Восстановить доступ',
	component: Forgot
}

let ResetPasswordPage = {
	path: '/reset/:token',
	props: true,
	name: 'Установка нового пароля',
	component: ResetPassword
}

let registerPage = {
	path: '/register',
	name: 'Регистрация',
	component: Register
}

let registerReferralPage = {
	path: '/register/:referral',
	props: true,
	name: 'Регистрация',
	component: Register
}

let activateUserAccountPage = {
	path: '/activate/:token',
	props: true,
	name: 'Активация аккаунта',
	component: UserActivate
}

let shop = {
	path: '/shop',
	component: DashboardLayout,
	redirect: '/shop/index',
	children: [
		{
			path: 'index',
			name: 'Магазин',
			component: ShopIndex
		}
	]
}


const routes = [
	loginPage,
	registerReferralPage,
	forgotPage,
	registerPage,
	ResetPasswordPage,
	activateUserAccountPage,
	shop,
	{
		path: '/',
		component: DashboardLayout,
		redirect: '/shop'
	},
	{
		path: '/index',
		component: DashboardLayout,
		redirect: '/shop'
	},
	{
		path: '/referral',
		component: DashboardLayout,
		redirect: '/referral/index',
		children: [
			{
				path: '/referral/index',
				name: 'Мои Приборы учёта',
				component: ReferralIndex
			}
		]
	},
	{
		path: '*',
		component: NotFound
	}
]

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
 function view(name) {
 var res= require('../components/Dashboard/Views/' + name + '.vue');
 return res;
 };**/

export default routes



