import api from './index'

export default {
	getNewLink (data, payload) {
		return api.fetch('/app/referral/get-link', {
			method: 'GET'
		}, payload)
	},
	getLinkItems (data, payload) {
		return api.fetch('/app/referral/get-link-items', {
			method: 'GET'
		}, payload)
	}
}



